.todoapp {
  -webkit-animation: fadeIn 0.5s forwards;
          animation: fadeIn 0.5s forwards;
}

.info {
  opacity: 0;
  -webkit-animation: fadeIn 0.3s 0.1s forwards;
          animation: fadeIn 0.3s 0.1s forwards;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 80px, -10px) scale(0.8);
            transform: translate3d(0, 80px, -10px) scale(0.8);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 80px, -10px) scale(0.8);
            transform: translate3d(0, 80px, -10px) scale(0.8);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}
